.Back{
 background: -webkit-linear-gradient(left, #3931af, #00c6ff);
 margin-bottom:  0% ;
}
.reg1{
    background: -webkit-linear-gradient(left, #3931af, #00c6ff);
    margin-bottom: 1%;
    padding: 10%;
    padding-right: 10%; 
    width: 100%;
    height: 100%;
    border-radius: 12px;
    
}
.reg1-left{
    text-align: center;
    color: #fff;
    margin-top: 4%;
}

.reg1-right{
    background: #f8f9fa;
    border-top-left-radius: 10% 50%;
    border-bottom-left-radius: 10% 50%;
}
.reg1 .reg1-form{
    padding: 10%;
    margin-top: 10%;
    font-family: Arial, Helvetica, sans-serif;
}
.register-btn{
    float:center;
    margin-top: 10%;
    border: none;
    border-radius: 1.5rem;
    padding: 2%;
    background: #0062cc;
    color: #fff;
    font-weight: 600;
    width: 30%;
    cursor: pointer;
    margin-right:100%;
    
}


.reg-heading{
    text-align: center;
    margin-top: 5%;
    margin-bottom: -15%;
    color: #1b1c1d;
}
