h1 {
    color: coral;
}
.grid-container {
    columns: 5 200px;
    column-gap: 1.5rem;
    width: 90%;
    margin: 0 auto;
}
.grid-container div {
    width: 150px;
    margin: 0 1.5rem 1.5rem 0;
    display: inline-block;
    width: 100%;
    border: solid 2px black;
    padding: 5px;
    box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.5);
    border-radius: 5px;
    transition: all 0.2s ease-in-out;
}
/* .grid-container a:hover img {
		filter: grayscale(0);
	  } */
.grid-container a:hover {
    border-color: coral;
}
.gallery {
    /* filter: grayscale(100%); */
    border-radius: 5px;
    transition: all 0.2s ease-in-out;
    margin-bottom: 1em;
}
